
.projects-section {
  padding: 40px;
  max-width: 1200px;
  margin: 0 auto;
}


.section-title {
  font-size: 2.5rem;
  text-align: center;
  color: #333;
  margin-bottom: 40px; 
  font-weight: bold;
}


.projects-container {
  display: flex;
  flex-direction: column;
  gap: 40px;
}


.project {
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}


.project-title {
  font-size: 1.8rem;
  font-weight: bold;
  color: #333;
  text-align: center;
  margin-bottom: 20px;
}


.project-content {
  display: flex;
  flex-direction: row;
  align-items: center;
}


.project-image {
  width: auto;
  height: 200px; 
  object-fit: cover;
  margin-right: 20px;
}


.project-info {
  flex: 1;
  text-align: left;
}


.project-description {
  font-size: 1.2rem;
  margin-bottom: 10px;
  color: #333;
  line-height: 1.6;
}


.project-tech {
  font-size: 1rem;
  color: #007bff;
  font-weight: bold;
}
